.introduction__inner {
  padding: 16px;
  /* display: flex;
  /* justify-content: space-between; */

  @media (max-width: 800px){ 
      flex-direction: column;
  }

  .introduction__title {
      position: sticky;
      top: 55px;
      left: 0;
      width: 70%;
      height: 5vw;
      font-size: 4vw;
      font-weight: 900;
      line-height: 1.4;
      text-transform: uppercase;
      color: var(--black100);
      border-bottom: 0.4vw solid var(--black100);
      justify-content: center;
      margin: 4vh 0;
      background-color: #f3ede8;

      @media (max-width: 800px){ 
          width: 100%;
          margin-bottom: 10vw;
          font-size: 30px;
          height: auto;
          top: 68px;
          background-color: var(--mainBg-color);
      }

      em {
          font-size: 1.25rem;
          font-weight: 400;
          line-height: 2;
      }
  }
  .introduction__desc {
      font-family: "Paperlogy-8ExtraBold";
      width: 100%;

      @media (max-width: 800px){ 
          width: 100%;
      }

      span {
          font-size: 4vw;
          line-height: 1;
          font-weight: 900;
          line-height: 1.3;
          font-family: "Paperlogy-8ExtraBold";

          @media (max-width: 800px){ 
              font-size: 20vw;
          }
      }
      h3 {
          font-size: 1.5rem;
        //   text-decoration: underline;
          text-underline-position: under;
          margin-bottom: 1vw;
          margin-top: 15px;
      }
      p {
          font-size: 1.1rem;
      }
  }

  .introduction_head {
    display: flex;
    flex-direction: column;
    margin-right: 2rem;
    font-size: 1.25rem;
    color: #353535;
    font-family: 'Pretendard-Regular';
  }

  .introduction__education {
    width: 45%;
    font-size: 2.3rem;
    text-transform: uppercase;
    margin: 1.5rem 0;
    border-bottom: 0.2vw solid var(--black100);
  }

  .introduction__skill {
    width: 45%;
    font-size: 2.3rem;
    text-transform: uppercase;
    margin: 1.5rem 0;
    border-bottom: 0.2vw solid var(--black100);
  }

  .introduction_date {
    font-size: 1.5rem !important;
    margin-right: 1.5rem !important;
  }

  .introduction_career {
    display: flex;
    font-family: "Paperlogy-8ExtraBold";
    align-items: center;
    font-size: 1.3rem !important;
    margin-bottom: 10rem;
  }

  .introduction_skill {
    display: flex;
    font-family: "Paperlogy-8ExtraBold";
    align-items: center;
    font-size: 1.3rem !important;
  }

  // .introduction_skill span {
  //   font-size: 1.3rem !important;
  // }

  .prjt01 {
    color: #ff5d00;
  }

  .prjt02 {
    color: #7DAF7D;
  }

  .prjt03 {
    color: #96B4DC;
  }

  .prjt04 {
    color: #888583;
  }
  
  .prjt05 {
    color: #DB9700;
  }

  .prjtdetail {
    margin-bottom: 30vh;
    display: flex;
    flex-direction: column;
  }

  .prjtdiv {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
  }

  .prjtdiv span {
    width: 100%;
    font-size: 1.5rem;
    margin: 1.5rem 0;
  }

  .prjtdetail span {
    font-size: 1.2rem;
    margin-top: 10px;
    width: 150px;
  }

  .introductionskill {
    margin-left: 3vh;
  }

  .introductionskill span{
    font-size: 1.2rem !important;
  }

  .prjtimportant {
    width: 100% !important; 
  }

  .prjtimportant span {
    font-size: 1.2rem;
    margin: 0 !important;
  }

  .java:hover {
    color: #f84b20;
  }
  .javascript:hover {
    color: #e7cf1b;
  }
  .react:hover {
    color: #61DAFB
  }
  .spring:hover {
    color: #65d61a
  }
  .nodejs:hover {
    color: #339933;
  }
  .oracle:hover {
