    color: #0769AD;
  }
  .sts3:hover {
    color: #6db33f;
  }
  .kakaoapi:hover {
    color: #ffcd00;
  }
  .jpa:hover {
    color: #59666C;
  }
  .kakaomobilityapi:hover {
    color: #ffcd00;
  }
  .reactnative:hover {
    color: #61DAFB;
  }
  .vite:hover {
    color: #646CFF;
  }
  .springboot:hover {
    color: #6DB33F;
  }
  .springmvc:hover {
    color: #2F6F44;
  }
  .html:hover {
    color: #E34F26;
  }
  .css:hover {
    color: #2965F1;
  }
  .mybatis:hover {
    color:#DC382D;
  }
  .vscode:hover {
    color: #007ACC;
  }
  .git:hover {
    color: #F05032;
  }
