  }

  .Introduction__desc img {
    max-width: 80%; /* 이미지가 부모 요소를 넘어가지 않도록 설정 */
    height: auto;    /* 비율에 맞게 높이 조정 */
    margin-top: 10px; /* 위쪽에 약간의 여백 추가 */
  }

  .introduction__item {
    margin-top: 30px;
    margin-bottom: 30vh;
}
  .prjt1page:hover {
    color: #ff5d00;
  }
  .prjt2page:hover {
    color: #7DAF7D;
  }

  .prjt3page:hover {
    color: #96B4DC;
  }

  .sideprjt1page:hover {
    color: #888583;
  }

  .sideprjt2update {
    margin-right: 1rem;
  }
  .sideprjt2update:hover {
    color: #b700db;
  }

  .sideprjt2page:hover {
    color: #DB9700;
  }


}

@keyframes slideInLeft {
    from{
        transform: translateX(-100%);
        opacity: 0;
    }
    to {
        transform: translateX(0);
        opacity: 1;
    }
}

@keyframes slideInRight {
    from {
        transform: translateX(100%);
        opacity: 0;
    }
    to {
        transform: translateX(0);
        opacity: 1;
    }


}

.introduction__desc {
  width: 100%;
  height: 100%;

}

