/* 수트체 */
@font-face {
  font-family: 'SUIT-Regular';
  src: url('https://fastly.jsdelivr.net/gh/projectnoonnu/noonfonts_suit@1.0/SUIT-Regular.woff2') format('woff2');
  font-weight: normal;
  font-style: normal;
}

/* 페이퍼로지 */
@font-face {
  font-family: 'Paperlogy-8ExtraBold';
  src: url('https://fastly.jsdelivr.net/gh/projectnoonnu/2408-3@1.0/Paperlogy-8ExtraBold.woff2') format('woff2');
  font-weight: 800;
  font-style: normal;
}

.project__inner {
  padding: 16px;
}
.project__title {
  position: sticky;
  top: 70px;
  left: 0;
  width: 70%;
  height: 5vw;
  font-size: 4vw;
  font-weight: 900;
  line-height: 1.4;
  
  font-family: var(--lilita);
  text-transform: uppercase;
  color: var(--black100);
  border-bottom: 0.4vw solid var(--black100);
  margin-bottom: 100px;
}
.project__title em {
  font-size: 1.25rem;
  font-weight: 400;
  line-height: 2;
  // font-family: var(--mainKor-font);
  font-family: 'Paperlogy-8ExtraBold';

}
.project__item {
  height: 70vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  position: sticky;
  left: 0;
}
.project__item.s1 {
  background-color: #ff9558;
  top: 180px;
}
.project__item.s2 {
  background-color: #7DAF7D;
  top: 200px;
}
.project__item.s3 {
  background-color: #96B4DC;
  top: 220px;
}
.project__item.s4 {
  background-color: #afa7a2;
  top: 240px;
}
.project__item.s5 {
  background-color: #EAC696;
  top: 240px;
}
.project__item .num {
  font-size: 5vw;
  line-height: 1;
  font-weight: 900;
  font-family: var(--mainNum-font);
  position: absolute;
  left: 1vw;
  top: 1vw;
}
.project__item .text {
  text-align: center;
  margin-bottom: 1rem;
}
.project__item .text > div {
  font-size: 3vw;
  line-height: 1.3;
  font-weight: 900;
  text-transform: uppercase;
  // text-decoration: underline;
  // font-family: var(--mainNum-font);
  font-family: 'Paperlogy-8ExtraBold';
}

.project__item .text .prjtsize {
  font-size: 2.5vw;
}


.project__item .title {
  font-family: 'Paperlogy-8ExtraBold';
  // font-family: var(--mainKor-font);
  margin-bottom: 1rem;
}
.project__item .btn a {
  text-decoration: underline;
  text-transform: uppercase;
  color: var(--black100);
  font-size: 1.3rem;
  transition: all 0.3s;
}
.project__item .btn a:hover {
  color: #F2620F;
}
.project__item .info {
  position: absolute;
  left: 0;
  bottom: 3%;
  width: 98%;
  margin-left: 1%;
  border-top: 1px solid var(--black);
  border-bottom: 1px solid var(--black);
  display: flex;
  justify-content: space-between;
  font-family: 'SUIT-Regular';
}
.Project__item .info span {
  font-family: var(--mainKor-font);
  font-size: 12px;
  padding: 3px 0;
  text-transform: uppercase;
}



//반응형
@media (max-width: 800px){
  .project__title {
      width: 100%;
      margin-bottom: 10vw;
      font-size: 30px;
      height: auto;
      top: 68px;
      z-index: 1000;
      background-color: var(--mainBg-color);
  }
  .project__item.s1 {
      top: 140px;
  }
  .project__item.s2 {
      top: 160px;
  }
  .project__item.s3 {
      top: 180px;
  }
  .project__item.s4 {
      top: 200px;
  }
  .project__item .num {
      font-size: 80px;
      left: 20px;
      top: 20px;
  }
  .project__item .text > div {
      font-size: 26px;
  }
  .project__item .text .prjt2size {
    font-size: 26px;
  }

  .project__item .info {
      flex-direction: column;
      width: 90%;
      margin-left: 5%;
      padding: 5px 0;
  }
}