#footer {
  font-family: var(--mainKor-font);
  background-color: #001d3d;
  color: #fff;

}
.footer__inner {
  padding: 16px;

  .footer__text {
      font-size: 12px;
      text-transform: uppercase;
      border-top: 1px solid var(--white);
      border-bottom: 1px solid var(--white);
      display: flex;
      justify-content: space-between;
  }
  .footer__info {
      display: flex;
      justify-content: space-between;
      margin: 100px 0;

      @media (max-width: 800px){
          flex-direction: column;
      }

      .left {
          width: 40%;

          @media (max-width: 800px){
              width: 100%;
          }

          .title {
              border-top: 2px solid var(--white);
              border-bottom: 1px solid var(--white);
              padding: 20px 0;
              text-transform: uppercase;
              font-weight: 700;
              // background: url(../../img/arrow.svg) no-repeat right 20px;
              transition: all 0.3s;

              &:hover {
                  background-color: var(--white400);
                  padding-left: 30px;
                  // border-color: transparent;  
              }
          }
          .desc {
              font-size: 12px;
              padding-top: 20px;

              @media (max-width: 800px){
                  margin-bottom: 40px;
              }
          }
      }

      .right {
          width: 50%;

          @media (max-width: 800px){
              width: 100%;
          }

          h3 {
              border-top: 2px solid var(--white);
              font-size: 10px;
              margin-bottom: 40px;
              padding-top: 10px;
          }
          ul {
              border-top: 1px solid var(--white);

              li {
                  border-bottom: 1px solid var(--white);

                  a {
                      padding: 14px 0;
                      font-size: 14px;
                      display: inline-block;
                      text-transform: uppercase;
                  }
                  em {
                      font-size: 12px;
                      padding-left: 10px;
                      opacity: 0;
                      transition: all 0.3s;
                  }

                  &:hover {
                      em {
                          opacity: 1;
                      }
                  }
              }
          }
      }
  }
  .footer__right {
      text-align: center;
      text-transform: uppercase;
      font-size: 12px;
      margin-bottom: 20px;
  }
}
